export default {
  name: 'wv-navbar-item',

  props: {
    id: String,
    disabled: Boolean
  },

  computed: {
    isSelected () {
      return this.id === this.$parent.value
    },

    style () {
      return {
        borderWidth: this.$parent.lineWidth + 'px',
        borderColor: this.$parent.activeColor,
        color: this.isSelected ? this.$parent.activeColor : this.$parent.color
      }
    }
  }
}



