import Cell from '../cell/index'

export default {
  name: 'wv-search',

  components: {
    Cell
  },

  props: {
    value: String,
    autofocus: Boolean,
    show: Boolean,
    placeholder: {
      type: String,
      default: '搜索'
    },
    cancelText: {
      type: String,
      default: '取消'
    },
    result: Array
  },

  data () {
    return {
      isActive: false,
      currentValue: this.value
    }
  },

  mounted () {
    if (this.autofocus) {
      console.log('fuck')
      this.$refs.searchInput.focus()
      this.isActive = true
    }
  },

  methods: {
    textClick (e) {
      // focus the input
      this.$refs.searchInput.focus()
      this.isActive = true
    },

    // 清除输入
    searchClear () {
      this.currentValue = ''
    },

    // 取消搜索
    searchCancel () {
      this.searchClear()
      this.isActive = false
    }
  },

  watch: {
    currentValue (val) {
      this.$emit('input', val)
    },

    value (val) {
      this.currentValue = val
    }
  }
}



