const CONFIRM_TEXT = '确定'
const CANCEL_TEXT = '取消'

export default {
  name: 'wv-dialog',

  props: {
    skin: {
      type: String,
      default: 'ios'
    },
    title: String,
    message: String,
    confirmText: {
      type: String,
      default: CONFIRM_TEXT
    },
    cancelText: {
      type: String,
      default: CANCEL_TEXT
    },
    showConfirmBtn: {
      type: Boolean,
      default: true
    },
    showCancelBtn: {
      type: Boolean,
      default: true
    }
  },

  data () {
    return {
      value: false
    }
  },

  methods: {
    handleAction (action) {
      this.value = false
      if (action === 'confirm') {
        let callback = this.callback
        callback(action)
      }
    }
  }
}



