export default {
  name: 'wv-checklist',

  props: {
    max: Number,
    title: String,
    align: String,
    options: {
      type: Array,
      required: true
    },
    value: Array
  },

  data () {
    return {
      currentValue: this.value
    }
  },

  computed: {
    limit () {
      return this.max < this.currentValue.length
    }
  },

  watch: {
    currentValue (val) {
      if (this.limit) val.pop()
      this.$emit('input', val)
      this.$emit('change', val)
    },

    value (val) {
      this.currentValue = val
    }
  }
}



