export default {
  name: 'wv-popup',

  props: {
    value: Boolean,
    height: {
      type: String | Number,
      default: 'auto'
    },
    hideOnMask: {
      type: Boolean,
      default: true
    },
    maskBackgroundColor: {
      type: String,
      default: ''
    },
    backgroundColor: {
      type: String,
      default: '#fff'
    }
  },

  data () {
    return {
      currentValue: this.value
    }
  },

  computed: {
    style () {
      let ret = { backgroundColor: this.backgroundColor }

      if (this.height === 'auto') {
        ret.height = 'auto'
      } else {
        ret.height = parseInt(this.height) + 'px'
      }

      return ret
    }
  },

  methods: {
    maskClick (e) {
      if (!this.hideOnMask) return
      this.currentValue = false
    }
  },

  watch: {
    value (val) {
      this.currentValue = val
    },

    currentValue (val) {
      this.$emit('input', val)

      if (val) {
        this.$emit('show')
      } else {
        this.$emit('hide')
      }
    }
  }
}



