import WvIcon from '../icon/index'

export default {
  components: {
    WvIcon
  },

  props: {
    visible: {
      default: true
    },
    icon: {
      type: String,
      default: 'success-no-circle'
    },
    type: {
      type: String,
      default: 'success'
    },
    message: {
      type: String,
      default: ''
    }
  },

  computed: {
    style () {
      if (this.type === 'text') {
        const messageLength = this.message.length + 2

        return {
          width: messageLength + 'em',
          marginLeft: '-' + (messageLength / 2) + 'em'
        }
      }

      return {}
    }
  }
}



